@import url('https://fonts.googleapis.com/css2?family=Poppins:ital,wght@0,100;0,200;0,300;0,400;0,500;0,600;0,700;0,800;0,900;1,100;1,200;1,300;1,400;1,500;1,600;1,700;1,800;1,900&display=swap');

body, html{
    height: 100%;
    width: 100%;
    margin: 0px 0px 0px 0px;
}

@media (max-width: 2000px) {
    #cmAll {
      transform: scale(0.90);
      transform-origin: top;
    }
  }

@media (max-width: 1332px) {
    .editInputs{
      margin-top: 20px;
    }
  }
  

.userblock{
    font-family: 'Poppins';
    font-size: 15px;
    background-color: white;
    margin: 20px;
    vertical-align: middle;
    min-width: 700px;
    border-radius: 5px;
    border: 1px solid rgba(0, 0, 0, 0.15);
    box-shadow: 5px 5px 10px 0px rgba(0, 0, 0, 0.15);
    min-height: 85px;
    font-size: 18px;
    font-family: 'Poppins';
    font-weight: 500;
    display: flex;
    flex-wrap: wrap; /* Allows for wrapping if the container width is too small */
    gap: 10px; /* Ensures 10px space between each child */
    align-items: center; /* Centers items vertically */
    justify-content: space-evenly; /* Distributes items evenly with space around them */
    /* Other styles remain unchanged */
}

#addClientBlock input{
    height: 35px;

    border-radius: 5px;
    color: black;
    font-size: 14px;
    font-weight: 500;
}

.addFirstName, .addLastName, .addPhoneNumber{
    border: 1.5px solid black;
}


.firstName, .lastName, .phoneNumber, .date {
    text-align: center;
    vertical-align: middle;
    
}

.firstName{
    width: 150px;
}

.lastName{
    width: 200px;
}

.phoneNumber{
    width: 110px;
}

.date{
    width: 120px;
    margin-left: 10px;
}

.bundleclose{
    display: flex;
    align-items: center;
    justify-content: space-evenly;
}

.close-icon {
    cursor: pointer;
    color: #969696;
    font-size: 18px;
    margin-left: 5px;
    margin-right: 10px;
    vertical-align: middle;
    text-align: left;
}

.groupblock, .addgroup{
    font-size: 12px;
    height: 20px;
    background-color: #a1ccd1;
    padding: 8px 12px;
    text-align: center;
    vertical-align: middle;
    color: white;
    border-radius: 3px;
    display: flex;
    align-items: center; /* Vertically aligns the text */
    justify-content: center;
    max-width: 200px;
}

.addgroup{
    font-size: 12px;
    background-color: #0f83f7;
    width: 82px;
    cursor: pointer;
}

.groupcontainer{
    margin: 20px 10px;
    display: grid;
    grid-template-columns: repeat(1, 1fr);
    gap: 10px;
    justify-items: start;

    vertical-align: middle;
}


.edit, .delete{
    background-color: #0f83f7;
    text-align: center;
    color: white;
    height: 36px;
    min-width: 85px;
    width: 85px;
    border-radius: 5px;
    font-family: 'DM Sans', sans-serif;
    border: none;
    line-height: 0px;
    cursor: pointer;
}

.addClient{
    background-color: #0f83f7;
    color: white;
    width: 85px;
    border-radius: 5px;
    margin-right: 10px;
    font-family: 'Poppins', sans-serif;
    border: none;
    min-height: 40px;
}

.delete{
    background-color: #f54940;
    height: 25px;
    font-size: 12px;
    margin-top: 5px;
    
}

.delete:hover {
    background-color: red;
}

.editstack{
    display:flex;
    flex-direction: column;
}



.cm-top-headers{
    display: flex;
    width: 100%;
    justify-content: center;
    margin-top: 15px;
}

#cm-toggle-add-client{
    background-color: #0f83f7;
    color: white;
    width: 125px;
    border-radius: 5px;
    margin-right: 10px;
    font-family: 'DM Sans', sans-serif;
    border: none;
    text-align: center;
    height: 40px;
    line-height: 40px;
    box-shadow: 5px 5px 10px 0px rgba(0, 0, 0, 0.15);
    cursor: pointer;
    
}


#cm-search{
    background-color: #ffffff;
    color: black;
    border-radius: 5px;
    margin-right: 10px;
    font-family: 'DM Sans', sans-serif;
    border: none;
    text-align: center;
    height: 40px;
    border: 1px solid rgba(0, 0, 0, 0.15);
    box-shadow: 5px 5px 10px 0px rgba(0, 0, 0, 0.15);
    display: flex;
    text-align: center;
    justify-content: space-around;
    cursor: pointer;
    margin-left: 20px;
}

#cm-search input{
    font-size: 15px;
    vertical-align: middle;
    margin-left: 20px;
    border: 0px;
    width: 100px;
}
#cm-search input:focus{
    font-size: 15px;
    vertical-align: middle;
    margin-left: 20px;
    border: 0px solid white;
    outline: none;
}

#content{
    min-width: 820px;
}




#cm-search-icon{
    height: 20px;
    margin: auto 10px;
}


.date-input {
    padding: 8px;
    border-radius: 5px;
    border: 1px solid #ccc;
    font-family: 'Poppins', sans-serif;
    font-size: 15px;
}
  
.date-time-selectors {
    justify-content: center;
    align-items: center;
}




/* Adjusting the container to use flexbox */
#addClientBlock .date-time-selectors {
    display: flex;
    flex-wrap: wrap; /* Allows items to wrap as needed */
    align-items: center; /* Aligns items vertically */
    justify-content: start; /* Aligns items to the start */
    margin: 10px 0;
  }
  
  #addClientBlock .date-time-selectors div {
    display: flex;
    flex-direction: column;
    align-items: center;
    margin-right: 5px; /* Adjust or remove to control spacing between elements */
  }
  
  #addClientBlock .date-time-selectors label {
    margin-bottom: 5px;
    font-size: 14px;
    color: #000;
    text-align: center;
  }
  
  #addClientBlock .date-time-selectors select {
    padding: 5px 10px;
    border-radius: 5px;
    border: 1.5px solid black;
    background-color: white;
    color: black;
    cursor: pointer;
  }
  





/* Style adjustments for the date selectors */
.date-selectors {
    display: flex;
    align-items: center;
}



.date-separator {
    padding: 0 5px; /* Adjust as needed for visual appearance */
    font-size: 16px; /* Ensures the '/' is clearly visible */
}






/* You might need to adjust the width of the firstName, lastName, and phoneNumber classes to ensure they fit well with the new date selectors. */

.firstName, .lastName, .phoneNumber {
    flex: none; /* Prevents these inputs from growing to fill extra space */
    /* Define explicit widths if necessary, considering the 10px gap */
}

/* Ensure the input and select elements have consistent height and styling */
#addClientBlock input, #addClientBlock select {
    height: 35px;
    color: black;
    text-align: center;
}

.appointment-date-container {
    display: flex;
    flex-direction: column;
    align-items: center; /* Center-aligns items horizontally */
    gap: 5px; /* Space between elements */
    padding-top: 10px; /* Adds padding to the top of the stack */
    padding-bottom: 10px; /* Adds padding to the bottom of the stack */
}

.appointment-date-label {
    font-weight: 500;
    text-align: center; /* Ensures the label text is centered */
}

.date-selectors, .time-selectors {
    display: flex;
    align-items: center; /* Centers items vertically */
    gap: 5px; /* Space between selectors */
    justify-content: center; /* Centers the selectors horizontally within their container */

}



.date-separator, .time-separator {
    font-size: 16px; /* Ensures the ':' and '/' are clearly visible */
    text-align: center; /* Center-aligns the separators if necessary */
}



.group-popup {
    position: fixed; /* Fixed positioning to make it stick at the top */
    top: 10%; /* Align top */
    left: 50%; /* Center horizontally */
    transform: translateX(-50%); /* Adjust to precisely center the element */
    background: white;
    padding: 10px;
    border: 0px;
    width: auto; /* Adjust the width as needed */
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1); /* Optional: Adds a shadow for better visibility */
    z-index: 10; /* Ensure it's above other content */
    min-width: 150px;
    border-radius: 5px;
}

.group-popup div {
    display: flex; /* Use flexbox for alignment */
    align-items: center; /* Align items vertically */
    margin-bottom: 10px; /* Space out each group selection */

}

.group-popup input[type="checkbox"] {
    margin-right: 10px; /* Space between checkbox and label */
}

.group-popup button {
    margin-top: 20px; /* Space above the 'Add Groups' button */
    cursor: pointer; /* Change cursor on hover */
    padding: 10px 20px; /* Padding for button */
    border: none; /* Remove default border */
    background-color: #007bff; /* Example button color */
    color: white; /* Text color */
    border-radius: 5px; /* Rounded corners */
}

.dim-background {
    position: fixed;
    top: -100%;
    left: -100%;
    width: 800%;
    height: 800%;
    background: rgba(0, 0, 0, 0.6);
    z-index: 9; /* Ensure it's below the popup but above other content */
}

.group-popup {
    z-index: 10; /* Ensure this is above the dim background */
    /* Other styles remain unchanged */
}


.group-popup {
    pointer-events: auto; /* Ensure the popup can still capture clicks. */
}

.date-selectors select, .time-selectors select{
    border: 1.5px solid black;
    border-radius: 5px;
    height: 35px;
}

.no-select {
    -webkit-touch-callout: none; /* iOS Safari */
    -webkit-user-select: none;   /* Safari */
    -khtml-user-select: none;    /* Konqueror HTML */
    -moz-user-select: none;      /* Old versions of Firefox */
    -ms-user-select: none;       /* Internet Explorer/Edge */
    user-select: none;           /* Non-prefixed version, currently supported by Chrome, Edge, Opera and Firefox */
  }



  .popupCheckbox{
    -webkit-appearance: none;
    -moz-appearance: none;
    appearance: none;
    width: 25px; /* custom size */
    height: 25px; /* custom size */
 
    border-radius: 5px; /* rounded corners */
    border: 4px solid black;
    cursor: pointer;
    position: relative;
    outline: none;
    box-shadow: 0 3px 5px rgba(0, 0, 0, 0.3); /* shadow for 3D effect */
  }

  .popupCheckbox:checked{
    background: linear-gradient(145deg, #72a4ff, #11acff); /* different gradient for checked state */

  }

  .popupCheckbox:checked::after{
    content: '';
    position: absolute;
    left: 6px;
    top: 0px;
    width: 4px;
    height: 12px;
    border: solid white;
    border-width: 0 3px 3px 0;
    transform: rotate(45deg);
  }

  .checkboxLabel{
    font-size: 25px;
    margin-top: 5px;
  }





  .editInputs{

    text-align: center;
    height: 35px;
    border: 1.5px solid black;
    border-radius: 5px;
    color: black;
    font-weight: 500;
    font-size: 17px;
  }

  .yearSelector{
    width: 50px;
    height: 33px;
}



.modal-overlay {
    position: fixed; /* Use fixed positioning to cover the whole screen */
    top: -100%;
    left: -100%;
    width: 800%;
    height: 800%;
    background-color: rgba(0, 0, 0, 0.5); /* Dim the background with semi-transparent black */
    z-index: 10; /* Ensure it's above other content but below the popup */
}








.yearSelector {
    border: 1.5px solid black;
    border-radius: 5px;
    text-align: center;
}


#addUserYear{
    border: 1.5px solid black;
}