@import url('https://fonts.googleapis.com/css2?family=DM+Sans:opsz,wght@9..40,500&family=Kanit:wght@500&family=Raleway:ital,wght@0,100..900;1,100..900&family=Roboto:wght@300;400;500;700;900&display=swap');


html{
    background-color: #f0f0f0;
}

#amAll{
    text-align: center;
    font-family: "Roboto";
    font-weight: 400;
}

@media (max-width: 2000px) {
    #amAll {
      transform: scale(0.75);
      transform-origin: top;
    }

    
  }
  

#amTitle{

    width: 450px;
    margin: auto;
    font-size: 32px;
    margin-bottom: 75px;
    margin-top: 50px;
}

@media (max-width: 2000px) {
    #amTitle {
      transform: scale(0.75);
      margin-bottom: 25px;
        margin-top: 25px;
    }

    
  }
  



#AMmessageBlock{
    background-color: white;
    width: 600px;
    margin: auto;
    border-radius: 8px;
    border: 1px solid rgba(4, 0, 0, 0.15);
    box-shadow: 5px 5px 10px 0px rgba(0, 0, 0, 0.15);
}

#AMmessageBlock #amMainText{
    min-width: 500px;
    max-width: 500px;
    min-height: 150px;
    border-radius: 8px;
    border: 3px solid gray;
    padding: 20px;
    font-size: 18px;
    font-family: "Roboto";
    font-weight: 500;
}

#amSmallText{
    min-width: 400px;
    max-width: 400px;
    border-radius: 8px;
    border: 3px solid gray;
    padding: 20px;
    font-size: 18px;
    font-family: "Roboto";
    font-weight: 500;
    min-height: 21.5px;
    height: 21.5px;
}


#AMmessageBlock h3{
    font-size: 28px;
}

#AMConfirmButton{
    background-color: #0f83f7;
    color: white;
    height: 36px;
    width: 200px;
    border-radius: 5px;
    font-family: 'Roboto', sans-serif;
    border: none;
    cursor: pointer;
    display: flex;
    align-items: center;
    justify-content: center;
    margin: auto;
    margin-top: 50px;
    margin-bottom: 20px;
    user-select: none;
    font-size: 18px;
    font-weight: 700;
}




#templateSelect{
    display: flex;
    justify-content: center;
    align-items: center;
    width: 100%; /* Set your desired width */
    height: 30px; /* Set your desired height */
    margin: 20px 0px;
}

#templateSelect h3{
    font-size: 20px;
}

.templateOpt{
    margin: 10px;
    border: 2.5px solid gray;
    width: 190px;
    height: 30px;
    display: flex;
    align-items: center;
    justify-content: center;
    border-radius: 5px;
    font-family: "Roboto";
    font-weight: 500;
    cursor: pointer;
    background-color: rgb(245, 245, 245);
    user-select: none;
}


.templateOptSelected{
    color: white;
    background-color: rgb(0, 211, 176);
    border: 2.5px solid rgb(0, 211, 176);
}

.selectedOpt{
    color: white;
    margin: 10px;
    border: 0px solid gray;
    width: 120px;
    height: 35px;
    display: flex;
    align-items: center;
    justify-content: center;
    border-radius: 5px;
    font-family: "Roboto";
    font-weight: 500;
    cursor: pointer;
    background-color: rgb(0, 211, 176);
    user-select: none;
}


#amPreviewTitle{
    font-size: 22px;
    padding: 0px 0px 0px 0px;
    margin: 25px 0px 20px 0px;
}

#amPreview, #amPreview2{
    width: 500px;
    margin: auto;
    padding: 0px 0px 0px 0px;
    max-width: 500px;
    word-wrap: break-word;
}

.previewName1, .previewName2{
    color: white;
    border: 0px solid gray;
    padding: 0px 5px;
    border-radius: 5px;
    font-size: 18px;
    font-family: "Roboto";
    font-weight: 500;
    background-color: rgb(0, 211, 176);
    white-space: nowrap;
    vertical-align: middle;
}

.previewName2{
    margin-left: 7px;
}

#bottomText{
    
    width: 500px;
    margin: auto;
    font-size: 20px;
    line-height: 40px;
    margin-top: 60px;
}

#bottomInput{
    margin: 0px 10px;
    height: 40px;
    width: 65px;
    font-size: 30px;
    font-family: "Roboto";
    color: black;
    font-weight: 700;
    text-align: center;
    border: 3px solid black;
    border-radius: 5px;
}


.overlay {

    width: 100%;
    height: 10%;
    display: flex;
    justify-content: center;
    align-items: center;
    position: fixed;
    top: 40px;
    right: calc(0% - 150px);

}

.overlay-content {
    background-color: white;
    padding: 20px;
    border-radius: 10px;
    box-shadow: 0 0 15px rgba(0, 0, 0, 0.2);
    font-size: 24px;
    width: 400px;
    text-align: center;
    font-weight: 700;
    font-family: "Roboto";
}
